import React, { Component } from 'react';
import styled from 'styled-components';
import DateHandler from './DateHandler';
import TimeHandler from './TimeHandler';
import AmountHandler from './AmountHandler';
import { ButtonContainer } from '../Button';


export default class NavBar extends Component {
  render() {
    return (

        <NavWrapper>
          <div className="credBlock">
            <div className="credHeader">Дата</div>
            <DateHandler />
          </div>
          <div className="credBlock">
            <div className="credHeader">Время</div>
            <TimeHandler />
          </div>

          <div className="credBlock">
            <div className="credHeader">Сколько мест</div>
            <AmountHandler />
          </div>
          <div className="credBlock">
            <ButtonContainer className="search-btn nav-input">Найти столик</ButtonContainer>
          </div>
        </NavWrapper>

    )
  }
}

const NavWrapper = styled.nav`

display: flex;
flex-wrap: wrap;
justify-content: center;

.credHeader{
  font: 400 17px Montserrat;
  color: dimgrey;
  margin-bottom: 20px;
}
.credBlock{
  width: 16rem;
  align-self: flex-end;
  text-align: center;
}
.react-datepicker-wrapper, 
.react-datepicker__input-container{
  width: 100%;
}
@media(max-width:835px){
  .credBlock{
    width: 45% !important;
  }
  .nav-input{
      width: 100% !important;
      height: 40px;
  }
  .credHeader{
    display:none;
  }
}

@media(max-width:1024px){
  .credBlock{
    width: auto;
    margin-right: -1px;
  }
  .nav-input{
      width: 12rem;
  }
}
`