import React, { Component } from 'react';
import styled from 'styled-components';
import Portal from './Portal';


export default class TimeHandler extends Component {

  state = {
    active: false
  }

  createButtons = () => {
    let tables = []
    for (let i = 10; i <= 23; i++) {
      const timeBtn = React.createElement('button', { className: 'hoursBtn' }, i + ":00")
      tables.push(timeBtn)
      const timeBtn2 = React.createElement('button', { className: 'hoursBtn2' }, i + ":30")
      tables.push(timeBtn2)

    }
    return tables


  }
  toggleTimeBlock() {
    const currentState = this.state.active;
    this.setState({ active: !currentState })
  }

  // windowClick = (event) => {
  //   var timeBlock = document.getElementsByClassName('timeBlock')[0]
  //   var timeHandler = document.getElementsByClassName('timeHandler')[0]
  //   if (event.target !== timeHandler) {
  //     if (event.target !== timeBlock && timeBlock.classList.contains("show-modal")) {
  //       this.toggleTimeBlock();
  //     }
  //   }
  // }
  
   windowClick = (event) => {
    const modal = document.querySelector('.modal');
    if (event.target === modal) {
        this.toggleTimeBlock();
    }
}

  componentDidMount() {

    const timeHandler = document.getElementsByClassName('timeHandler')[0]
    const nowHours = new Date().getHours();
    if (nowHours > 10) {
      timeHandler.value = nowHours + 1 + ":00";
    }
    else {
      timeHandler.value = "10:00"
    }

    const timeBtn = document.getElementsByClassName('hoursBtn')
    const timeBtn2 = document.getElementsByClassName('hoursBtn2')

    for (let tb of timeBtn) {
      if (tb.innerHTML.substring(0, 2) <= nowHours) {
        tb.classList.add('hoursDisabled')
      }
      tb.addEventListener('click', function () {
        timeHandler.value = this.innerHTML;
      });
    }

    for (let tb of timeBtn2) {
      if (tb.innerHTML.substring(0, 2) <= nowHours) {
        tb.classList.add('hoursDisabled')
      }

      tb.addEventListener('click', function () {
        timeHandler.value = this.innerHTML;
      });
    }

    document.addEventListener('click', this.windowClick);
  }

  render() {
    return (
      <React.Fragment>
        <input type="text" className="timeHandler nav-input" onClick={() => this.toggleTimeBlock()} readOnly />
        <Portal>
          <TimeWrapper>
            <div className={this.state.active ? 'modal show-modal' : "modal"}>
            <div className="timeBlock" onClick={() => this.toggleTimeBlock()}>
            {this.createButtons()}
            </div>
            </div>
           
          </TimeWrapper>
        </Portal>
      </React.Fragment>
    )
  }
}

const TimeWrapper = styled.div`
  .modal{
    display: block;
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    opacity: 0;
    visibility: hidden; 
  }

.timeBlock{
  max-width: 616px;
  margin: auto;
  margin-top: 40%;
}
.show-modal {
  opacity: 1;
  visibility: visible;
}
.hoursBtn, .hoursBtn2, .hoursDisabled{
  background: var(--mainGreen);
  border: none;
  margin: 4px;
  width: 80px;
  height: 40px;
  color: white;
  cursor: pointer;
  font: 500 13px Montserrat;
  &:hover{
    background: var(--darkGreen);
  }
}
.hoursDisabled{
  background: var(--notActive);
  color: #d0d0d0;
  cursor:default;
  pointer-events: none;
}

@media(max-width: 750px){
  .timeBlock{
    margin: 30px 5%;
  }
}
`