import React, { Component } from 'react';
import styled from 'styled-components';
import humanIcon from '../../static/human.svg';

export default class AmountHandler extends Component {
  state = {
    counter: 2,
  }

  handleAddClick = () => {
    if (this.state.counter < 7) {
      this.setState(({ counter }) => ({
        counter: ++counter
      }))
    }
  }
  handleSubClick = () => {
    if (this.state.counter > 2) {
      this.setState(({ counter }) => ({
        counter: --counter,
      }))
    }
  }


  render() {
    const { counter } = this.state;
    return (
      <AmountWrapper>
        <button className="sub-num" onClick={this.handleSubClick}>-</button>
          <input type="text" value={counter > 4 ? counter + " человек" : counter + " человека"} className="amount-input nav-input" readOnly />
        <button className="add-num" onClick={this.handleAddClick}>+</button>
      </AmountWrapper>
    )
  }
}

const AmountWrapper = styled.div`

position: relative;

.amount-input{
  cursor: default;
  text-align: center;
  padding: 0;
  padding-bottom: 2px;
}

.add-num, .sub-num{
  position: absolute;
  background: none;
  border: none;
  font-size: 20px;
  cursor: pointer;
}
.add-num{
  right: 30px;
  bottom: 6px;
}
.sub-num{
  left: 35px;
  bottom: 7px;
}
@media(max-width:835px){
  .add-num{
    right: 30px;
    bottom: 6px;
  }
  .sub-num{
    left: 45px;
    bottom: 7px;
  }
}

@media(max-width:1024px){
  .add-num{
    right: 11px;
  }
  .sub-num{
    left: 12px;
  }
}
`